:root {
    --page-transition-duration: 400ms;
}

.page {
    position: absolute;
}

.page-exit {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}

.page-exit-active {
    opacity: 0;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    -webkit-transition: opacity 400ms, -webkit-transform 400ms;
    transition: opacity 400ms, -webkit-transform 400ms;
    transition: opacity 400ms, transform 400ms;
    transition: opacity 400ms, transform 400ms, -webkit-transform 400ms;
    -webkit-transition: opacity var(--page-transition-duration), -webkit-transform var(--page-transition-duration);
    transition: opacity var(--page-transition-duration), -webkit-transform var(--page-transition-duration);
    transition: opacity var(--page-transition-duration), transform var(--page-transition-duration);
    transition: opacity var(--page-transition-duration), transform var(--page-transition-duration), -webkit-transform var(--page-transition-duration);
}

.page-enter {
    opacity: 0;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.page-enter-active {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: opacity 400ms, -webkit-transform 400ms;
    transition: opacity 400ms, -webkit-transform 400ms;
    transition: opacity 400ms, transform 400ms;
    transition: opacity 400ms, transform 400ms, -webkit-transform 400ms;
    -webkit-transition: opacity var(--page-transition-duration), -webkit-transform var(--page-transition-duration);
    transition: opacity var(--page-transition-duration), -webkit-transform var(--page-transition-duration);
    transition: opacity var(--page-transition-duration), transform var(--page-transition-duration);
    transition: opacity var(--page-transition-duration), transform var(--page-transition-duration), -webkit-transform var(--page-transition-duration);
}
